@font-face {
    font-family: 'PT Sans';
    src: url('../fonts/PTSans-Bold/PTSans-Bold.woff2') format('woff2'),
    url('../fonts/PTSans-Bold/PTSans-Bold.woff') format('woff'),
    url('../fonts/PTSans-Bold/PTSans-Bold.ttf') format('truetype');
    font-weight: bold;
    font-style: normal;
    font-display: swap;
}

@font-face {
    font-family: 'PT Sans';
    src: url('../fonts/PTSans-Regular/PTSans-Regular.woff2') format('woff2'),
    url('../fonts/PTSans-Regular/PTSans-Regular.woff') format('woff'),
    url('../fonts/PTSans-Regular/PTSans-Regular.ttf') format('truetype');
    font-weight: normal;
    font-style: normal;
    font-display: swap;
}

