.container{
  max-width: 1280px;
  width: 100%;
  margin:0 auto;
}

body{
  font-family: "PT Sans", sans-serif;
  font-weight: 400;
  font-size: 16px;
  color:#28292c;
}

.content{
  &-wrapper{
    display: grid;
    grid-template-columns: calc(70% - 20px) calc(30% - 20px);
    column-gap: 40px;
    row-gap: 40px;
    padding-top: 25px;
  }
  &__desc{
    padding-bottom: 30px;
    &::after{
      content: "";
      display: table;
      clear: both;
    }
  }
  &__img{
    float: left;
    margin: 0 30px 30px 0;
    img{
      width: 210px;
      height: 310px;
    }
  }
  &__text{
    text-align: justify;
    padding-top: 55px;
    ul{
      margin: 25px 0;
      li + li{
        margin-top: 25px;
      }
      li{
        display: flex;
        align-items: flex-start;
      }
      li:before{
        content: '';
        display: block;
        border: 5px solid transparent;
        border-left: 10px solid #333e7a;
        width: 10px;
        height: 5px;
        margin-top: 7px;
        margin-right: 15px;
      }
    }
  }
}

.text{
  font-size: 16px;
  font-weight: 400;
  line-height: 20px;
  &_small{
    font-size: 14px;
    line-height: 18px;
  }
  &-title{
    font-size: 22px;
    font-weight: 700;
    margin-bottom: 20px;
  }
}

p + p{
  margin-top: 25px;
}

.text_small + .text_small{
  margin-top: 8px;
}

.read-more{
  color:#333e7a;
  font-size: 14px;
  text-decoration: underline;
  text-underline-position: under;
}

.news{
  display: flex;
  align-items: flex-start;
  &-block{
    padding-top: 40px;
    border-top: 1px solid #d8d8d8;
  }
  &-grid{
    display: grid;
    grid-template-columns: repeat(2,minmax(0,1fr));
    column-gap: 40px;
    row-gap: 50px;
    margin-bottom: 40px;
  }
  &__img{
    width: 115px;
    height: 90px;
    flex-shrink: 0;
    background-color: #d4d4d4;
    margin-right: 12px;
    img{
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  &__name{
    font-size: 14px;
    line-height: 16px;
    transition: 0.3s color linear;
    &:hover{
      color:#343e7a;
    }
  }
  &__desc{
    display: flex;
    flex-direction: column;
  }
  &__date{
    color:#a1a1a1;
    font-size: 14px;
    margin-top: 5px;
    display: block;
  }
}

.aside{
  padding-top: 60px;
  &-block + &-block{
    margin-top: 35px;
  }
  &-title{
    display: flex;
    background-color: #333e7a;
    &__img{
      background-color: #dfa82a;
      flex-shrink: 0;
      padding: 25px;
      display: flex;
      align-items: center;
      justify-content: center;
      img{
        width: 55px;
        height: 55px;
        object-fit: contain;
      }
    }
    &__text{
      color:#fff;
      font-size: 24px;
      font-weight: 700;
      padding: 27px;
      text-transform: uppercase;
    }
  }
  &-block{
    &__title{
      text-transform: uppercase;
      text-align: center;
      padding: 25px 60px;
      color:#fff;
      font-size: 16px;
      font-weight: 700;
      background-color: #333e7a;
      &_orange{
        background-color: #dfa82a;
      }
    }
  }
  &-news{
    padding: 30px 0;
    border-bottom: 1px solid #d8d8d8;
    &::after{
      content: "";
      display: table;
      clear: both;
    }
    &__text{
      margin-bottom: 8px;
    }
    &__img{
      width: 115px;
      height: 90px;
      background-color: #d4d4d4;
      float: left;
      margin: 0 12px 8px 0;
      img{
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }
  }
}

.footer{
  padding: 25px;
  background-color: #333e7a;
  margin-top: 60px;
  color:#ffffff;
  &-row{
    display: flex;
    justify-content: space-between;
  }
  &-contacts{
    text-align: right;
    p + p{
      margin-top: 0px;
    }
  }
  &-privacy{
    margin-top: 15px;
  }
  p{
    font-size: 14px;
  }
}

@media screen and (max-width: 1280px) {
  .container{
    padding: 0 20px;
  }
}

@media screen and (max-width: 768px) {
  body{
    &.hidden{
      overflow: hidden;
    }
  }
  .main-nav{
    padding: 20px 0;
  }
  .nav-list{
    position: fixed;
    top: 0;
    left: 0;
    flex-direction: column;
    background-color: #333e7a;
    padding: 15px;
    opacity: 0;
    z-index: -1;
    transition: .3s all linear;
    height: 300px;
    overflow-y: auto;
    border-top: 1px solid #fff;
    &.active{
      opacity: 1;
      z-index: 99;
    }
  }
  .burger{
    display: flex;
  }
  .content{
    &-wrapper{
      grid-template-columns: 1fr;
    }
    &__img{
      img{
        width: 200px;
        height: 200px;
        object-fit: contain;
      }
    }
  }
}

@media screen and (max-width: 475px) {
  .footer-row{
    flex-direction: column;
  }
  .footer{
    text-align: center;
  }
  .footer-contacts{
    text-align: center;
    margin-top: 15px;
  }
  .header{
    padding: 20px 0;
    &__text{
      font-size: 16px;
    }
    &__title{
      font-size: 22px;
    }
  }
  .news-grid{
    grid-template-columns: 1fr;
    row-gap: 30px;
  }
  .content{
    &__img{
      width: 100%;
      img{
        width: 100%;
        height: 250px;
        object-fit: contain;
      }
    }
  }
  p + p{
    margin-top: 15px;
  }
  .aside-block + .aside-block{
    margin-top: 15px;
  }
  .aside{
    &-block{
      &__title{
        padding: 15px;
      }
    }
    &-title{
      &__img{
        padding: 15px;
      }
      &__text{
        padding: 15px;
        font-size: 18px;
        display: flex;
        align-items: center;
        justify-content: center;
      }
    }
  }
}