.header{
  padding: 50px 0;
  background: url("../img/header.png") center center no-repeat;
  background-size: cover;
  &__text{
    font-size: 20.5px;
  }
  &__title{
    font-size: 28px;
    font-weight: 700;
    text-transform: uppercase;
  }
}

.main-nav{
  background-color: #333e7a;
  &__wrapper{
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
.nav-list{
  display: flex;
  align-items: center;
  &__link{
    display: block;
    color:#fff;
    font-size: 18px;
    padding: 25px 22px;
    transition: 0.3s background-color linear;
    background-color: transparent;
    &:hover{
      background-color: #2b356d;
    }
  }
}

.burger{
  width: 30px;
  height: 20px;
  display: none;
  justify-content: space-between;
  flex-direction: column;
  &__item{
    flex-shrink: 0;
    width: 100%;
    height: 2px;
    border-radius: 2px;
    background-color: #fff;
    transition: 0.5s all linear;
  }
  &.active{
    .burger__item{
      &:nth-child(3){
        opacity: 0;
      }
      &:nth-child(1){
        transform: rotate(45deg) translateX(6px);
      }
      &:nth-child(2){
        transform: rotate(-45deg) translateX(6px);
      }
    }
  }
}